import React from 'react';
import styles from './Login.scss';
import classNames from 'classnames/bind';
import * as constants from "constants/Constants";

const cx = classNames.bind(styles);

const Login = ({visible, userId, password, error, onLogin, onChange, onKeyPress}) => {
    const handleChange = (e) => {
        const { value, name } = e.target;
        onChange({name, value});
    }
    return(
        <div className={cx('form')}>
            {/*<div onClick={onCancel} className={cx('close')}>&times;</div>*/}
            <div className={cx('title')}></div>
            <div className={cx('description')}>,  </div>
            <input name = 'userId' autoFocus type="text" placeholder=" " value={userId} onChange={handleChange} onKeyPress={onKeyPress}/>
            <input name = 'password' type="password" placeholder=" " value={password} onChange={handleChange} onKeyPress={onKeyPress}/>
            {error && <div className={cx('error')}> </div>}
            <button name = "classic" className={cx('login')} onClick={onLogin}></button>
            <button name='google' onClick={onLogin} className={cx('login-btn', 'google')} > </button>
            <button name='facebook' onClick={onLogin} className={cx('login-btn', 'facebook')}> </button>
            <button name='kakao' onClick={onLogin} className={cx('login-btn', 'kakao')}> </button>
        </div>
    )
};

export default Login;